import React from 'react';
import {Form, Input, Card, Button, Checkbox, message} from 'antd';
import TopNav from "../../components/top-nav";
import memoryUtils from "../../util/memoryUtils";
import { reqRoleAdd } from '../../api/index';

const AddRole = Form.create({ name: 'role-add' })(
    class extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                data: [
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    },
                    {
                        title: "",
                        display: 0
                    }
                ]
            }
        }
        validateName = (rule, value, callback) =>{
            if(!value){
                callback("")
            }else if(value.length>10){
                callback("10")
            }else{
                callback()
            }
        };
        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFields( async (err, values) => {
                if(!err) {
                    let arr = [];
                    this.state.data.forEach(item => {
                        item.display===1 ? arr.push(item.title) : arr.push();
                    });
                    let data=values;
                    data.createtime = new Date().getTime();
                    data.creater = memoryUtils.user.id;
                    data.permission = arr.join('-');
                    const response = await reqRoleAdd(data);
                    if(response.status === 0){
                        message.success(", ");
                        this.timerID = setTimeout(()=>{
                            this.props.history.push('/role')
                        }, 2000);
                    }
                }
            });
        };
        handleChange = (e, temp) => {
            let data = this.state.data;
            if(typeof(temp)==="object"){
                if(e.target.checked){
                    temp.forEach(item => {
                        data[item].display = 1
                    })
                }else{
                    temp.forEach(item => {
                        data[item].display = 0
                    })
                }
            }else{
                data[temp].display = 1 - data[temp].display
            }
            this.setState({data: data})
        };
        getTime = () => {
            let temp = new Date();
            let month = temp.getMonth()+1;
            return temp.getFullYear()+"-"+month+"-"+temp.getDate()+" "+temp.getHours()+":"+temp.getMinutes()
        };
        render() {
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            const form = this.props.form;
            return (
                <div>
                    <TopNav nav={['', '', '']}/>
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/role')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="">
                                {form.getFieldDecorator('createtime', {
                                    rules: [{required: true}],
                                    initialValue: this.getTime()
                                })(<Input disabled/>)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('creater', {
                                    rules: [{required: true}],
                                    initialValue: memoryUtils.user.username
                                })(<Input disabled/>)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('name', {
                                    rules: [{required: true, validator: this.validateName }],
                                })(<Input />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('permission', {
                                    initialValue: "perimission",
                                    rules: [{required: true}]
                                })(<Input style={{display: "none"}} />)}
                            </Form.Item>
                            <div style={{margin: "-53px 0 40px 340px"}}>
                                {/**/}
                                <Checkbox onChange={(e)=>this.handleChange(e, [0, 1])}></Checkbox>
                                <div style={{marginLeft: "30px"}}>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 0)}
                                        checked={this.state.data[0].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 1)}
                                        checked={this.state.data[1].display===1}
                                        style={{marginBottom: "10px"}}
                                    ></Checkbox><br/>
                                </div>
                                {/**/}
                                <Checkbox onChange={(e)=>this.handleChange(e, [2, 3, 4])}></Checkbox>
                                <div style={{marginLeft: "30px"}}>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 2)}
                                        checked={this.state.data[2].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 3)}
                                        checked={this.state.data[3].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 4)}
                                        checked={this.state.data[4].display===1}
                                        style={{marginBottom: "10px"}}
                                    ></Checkbox><br/>
                                </div>
                                {/**/}
                                <Checkbox onChange={(e)=>this.handleChange(e, [5, 6])}></Checkbox>
                                <div style={{marginLeft: "30px"}}>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 5)}
                                        checked={this.state.data[5].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 6)}
                                        checked={this.state.data[6].display===1}
                                    ></Checkbox><br/>
                                </div>
                                {/**/}
                                <Checkbox onChange={(e)=>this.handleChange(e, [7, 8])}></Checkbox>
                                <div style={{marginLeft: "30px"}}>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 7)}
                                        checked={this.state.data[7].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 8)}
                                        checked={this.state.data[8].display===1}
                                    ></Checkbox><br/>
                                </div>
                                {/**/}
                                <Checkbox onChange={(e)=>this.handleChange(e, [9, 10, 11])}></Checkbox>
                                <div style={{marginLeft: "30px"}}>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 9)}
                                        checked={this.state.data[9].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 10)}
                                        checked={this.state.data[10].display===1}
                                    ></Checkbox><br/>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 11)}
                                        checked={this.state.data[11].display===1}
                                        style={{marginBottom: "10px"}}
                                    ></Checkbox><br/>
                                </div>
                                {/**/}
                                <Checkbox onChange={(e)=>this.handleChange(e, [12])}></Checkbox>
                                <div style={{marginLeft: "30px"}}>
                                    <Checkbox
                                        onChange={(e)=>this.handleChange(e, 12)}
                                        checked={this.state.data[12].display===1}
                                        style={{marginBottom: "10px"}}
                                    ></Checkbox><br/>
                                </div>
                            </div>
                            <Form.Item label="">
                                {form.getFieldDecorator('note', {
                                    initialValue: ""
                                })(<Input.TextArea rows={4}/>)}
                            </Form.Item>
                            <Form.Item>
                                <Button style={{margin: "50px 0 0 460px"}} type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Card>
                </div>
            )
        }
    },
);

export default AddRole;








