import React from 'react';
import './index.less';
import Logo from '../../assets/images/logo.png';
import {Link,withRouter} from 'react-router-dom';
import {Menu,Icon} from 'antd';
// import menuList from '../../config/menuConfig';
import memoryUtils from "../../util/memoryUtils";

const {SubMenu}=Menu;

class LeftNav extends React.Component{
    constructor(props){
        super(props);
        this.state={
            menu: [],
            openKeys: [this.props.location.pathname]
        }
    }
    UNSAFE_componentWillMount = () => {
    
        let menu = [
            {
                key: "/home",
                title: "",
                icon: "home",
                display: 1
            },
            {
                key: "/order",
                title: "",
                icon: "account-book",
                display: 0,
                children: [
                    {
                        key: "/order",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/order-stage",
                        title: "",
                        display: 0
                    }
                ]
            },
            {
                key: "/product",
                title: "",
                icon: "shopping",
                display: 0,
                children: [
                    {
                        key: "/category",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/product",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/product-add",
                        title: "",
                        display: 0
                    }
                ]
            },
            {
                key: "/stock",
                title: "",
                icon: "appstore",
                display: 0,
                children: [
                    {
                        key: "/stock",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/inventory",
                        title: "",
                        display: 0
                    }
                ]
            },
            {
                key: "/customer",
                title: "",
                icon: "crown",
                display: 0,
                children: [
                    {
                        key: "/customer",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/customer-add",
                        title: "",
                        display: 0
                    }
                ]
            },
            {
                key: "/employee",
                title: "",
                icon: "team",
                display: 0,
                children: [
                    {
                        key: "/employee",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/user",
                        title: "",
                        display: 0
                    },
                    {
                        key: "/role",
                        title: "",
                        display: 0
                    }
                ]
            },
            {
                key: "/finance",
                title: "",
                icon: "area-chart",
                display: 0,
                children: [
                    {
                        key: "/finance",
                        title: "",
                        display: 0
                    }
                ]
            }
        ];
        memoryUtils.user.permission.forEach(item => {
            switch (item) {
                case '':
                    menu[1].display = 1;
                    menu[1].children[0].display = 1;
                    break;
                case '':
                    menu[1].display = 1;
                    menu[1].children[1].display = 1;
                    break;
                case '':
                    menu[2].display = 1;
                    menu[2].children[0].display = 1;
                    break;
                case '':
                    menu[2].display = 1;
                    menu[2].children[1].display = 1;
                    break;
                case '':
                    menu[2].display = 1;
                    menu[2].children[2].display = 1;
                    break;
                case '':
                    menu[3].display = 1;
                    menu[3].children[0].display = 1;
                    break;
                case '':
                    menu[3].display = 1;
                    menu[3].children[1].display = 1;
                    break;
                case '':
                    menu[4].display = 1;
                    menu[4].children[0].display = 1;
                    break;
                case '':
                    menu[4].display = 1;
                    menu[4].children[1].display = 1;
                    break;
                case '':
                    menu[5].display = 1;
                    menu[5].children[0].display = 1;
                    break;
                case '':
                    menu[5].display = 1;
                    menu[5].children[1].display = 1;
                    break;
                case '':
                    menu[5].display = 1;
                    menu[5].children[2].display = 1;
                    break;
                case '':
                    menu[6].display = 1;
                    menu[6].children[0].display = 1;
                    break;
                default:
                    break;
            }
        });
        this.setState({menu: menu})
    };
    rootSubmenuKeys = ['/order', '/product','/category','/stock', '/customer','/employee','/finance'];
    onOpenChange = openKeys => {
        const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
        if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
            this.setState({ openKeys });
        } else {
            this.setState({
                openKeys: latestOpenKey ? [latestOpenKey] : [],
            });
        }
    };
    closeSubmenuAll = () =>{
        this.setState({openKeys:[]});
    };
    render(){
        const path=this.props.location.pathname;
        return (
            <div className="left-nav">
                <Link to="/">
                    <div className="left-nav-header">
                        <img src={Logo} alt="logo"/>
                        <h1></h1>
                    </div>
                </Link>
                <Menu
                    selectedKeys={[path]}
                    mode="inline"
                    openKeys={this.state.openKeys}
                    onOpenChange={this.onOpenChange}
                    // style={{backgroundColor:"#0354d9"}}
                    theme="dark"
                >
                    {
                        this.state.menu.map((item) => {
                            if(!item.children){
                                return (
                                    <Menu.Item key={item.key} style={{display: item.display===0 ? "none" : "block"}}>
                                        <Link to={item.key}><Icon type={item.icon}/>{item.title}</Link>
                                    </Menu.Item>
                                )
                            }else{
                                return  (
                                    <SubMenu
                                        key={item.key}
                                        title={(<span><Icon type={item.icon}/>{item.title}</span>)}
                                        style={{display: item.display===0 ? "none" : "block"}}
                                    >
                                        {
                                            item.children.map(item => {
                                                return (
                                                    <Menu.Item key={item.key} style={{display: item.display===0 ? "none" : "block"}}>
                                                        <Link to={item.key}>{item.title}</Link>
                                                    </Menu.Item>
                                                )
                                            })
                                        }
                                    </SubMenu>
                                )
                            }
                        })
                    }
                </Menu>
            </div>
        )
    }
}

export default withRouter(LeftNav);