import React from 'react';
import {Button, Card, Form, Input, Radio, InputNumber, Steps, DatePicker, Icon} from 'antd';
import TopNav from "../../components/top-nav";
import Select from "antd/es/select";
import PicturesWall from "../../components/picture-wall/picture-wall";
import { addEmployeeList } from '../../api/index';

const AddEmployee = Form.create({name: 'add-employee-form'})(
    class extends React.Component{
        constructor(props) {
            super(props);
            this.state = {
                current: 0,
                value1: {},
                data: {},
                key: 'tab1'
            }
        }
        handleNext = () => {
            let field = ['name','id','gender','age','nation','birthday','origin','marriage','institution','degree','graduatetime'];
            this.props.form.validateFields(field,(err,values) => {
                if(!err){
                    let data = this.state.data;
                    let current = this.state.current;
                    for(let key in values){
                        if(values.hasOwnProperty(key)){
                            data[key] = values[key]
                        }
                    }
                    this.setState({
                        current: current+1,
                        data: data
                    })
                }
            })
        };
        handlePrevious = () => {
            let current = this.state.current;
            this.setState({current: current-1})
        };
        handleSubmit = (e) => {
            e.preventDefault();
            let field = ['profile','workid','entrytime','department','duty','level','tel','address','salary','note'];
            this.props.form.validateFields(field, async (err, values) => {
                if(!err){
                    let data = this.state.data;
                    let current = this.state.current;
                    for(let key in values){
                        if(values.hasOwnProperty(key)){
                            data[key] = values[key]
                        }
                    }
                    data.birthday = new Date(data.birthday).getTime();
                    data.graduatetime = new Date(data.birthday).getTime();
                    data.entrytime = new Date(data.birthday).getTime();
                    const response = await addEmployeeList(data);
                    if(response.status === 0){
                        this.setState({
                            data: data,
                            current: current+1
                        })
                    }
                }
            })
        };
        onTabChange = (key) => {
            this.setState({ key: key });
        };
        getDateTime = (birthday) => {
            let time = new Date(birthday);
            let month = time.getMonth()+1;
            return time.getFullYear()+'-'+month+'-'+time.getDate();
        };
        getMonthTime = (date) => {
            let time = new Date(date);
            let month = time.getMonth()+1;
            return time.getFullYear()+'-'+month;
        };
        render(){
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            const contentList = {
                tab1: (
                    <Form  {...formItemLayout} style={{marginTop: "40px", textIndent: "20px"}}>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.name}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.id}</span>
                        </Form.Item>
                        <Form.Item label="">
                        <span className="ant-form-text" style={{textIndent: "10px"}}>
                            {
                                this.state.data.gender===0 ? <span><Icon type="woman" /> </span> : <span><Icon type="man" /> </span>
                            }
                        </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.age}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.nation}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.getDateTime(this.state.data.birthday)}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.origin}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.marriage===0 ? "" : "" }</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.degree}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.institution}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.getMonthTime(this.state.data.graduatetime)}</span>
                        </Form.Item>
                    </Form>
                ),
                tab2: (
                    <Form {...formItemLayout} style={{marginTop: "40px", textIndent: "20px"}}>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.workid}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.getDateTime(this.state.data.entrytime)}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.department}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.duty}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">
                                {this.state.data.level===0 ? "" : this.state.data.level===1 ?  "" : ""}
                            </span>
                        </Form.Item>
                        <Form.Item label="Tel">
                            <span className="ant-form-text">{this.state.data.tel}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.address}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.salary} </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.note}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span style={{width: "320px", marginLeft: "20px"}}>
                                {
                                    !this.state.data.profile ? <span></span> :
                                        <img src={this.state.data.profile}
                                             alt=""
                                             style={{
                                                 width: "100px",
                                                 height: "100px",
                                                 padding: "7px",
                                                 marginRight: "10px",
                                                 borderRadius: "5px",
                                                 border: "1px solid lightgray"}}
                                        />
                                }
                            </span>
                        </Form.Item>
                    </Form>
                )
            };
            const steps = [
                {
                    title: "",
                    content: (
                        <Form {...formItemLayout} style={{marginTop: "40px"}}>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('name', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('id', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('gender', {
                                        rules: [{required: true}]
                                    })(
                                        <Radio.Group>
                                            <Radio value={1} style={{margin: "0 20px"}}></Radio>
                                            <Radio value={0}></Radio>
                                        </Radio.Group>
                                    )
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('age', {
                                        rules: [{required: true}]
                                    })(<InputNumber style={{width: "150px"}} />)
                                }
                                <span className="ant-form-text"></span>
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('nation', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('birthday', {
                                        rules: [{required: true}]
                                    })(<DatePicker format='YYYY/MM/DD' />)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('origin', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            
                            
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('marriage', {
                                        rules: [{required: true}]
                                    })(
                                        <Radio.Group>
                                            <Radio value={0} style={{margin: "0 20px"}}></Radio>
                                            <Radio value={1}></Radio>
                                        </Radio.Group>
                                    )
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('degree', {
                                        rules: [{required: true}]
                                    })(
                                        <Select placeholder="" style={{width: "187px"}}>
                                            <Select.Option value=''></Select.Option>
                                            <Select.Option value=''></Select.Option>
                                            <Select.Option value=''></Select.Option>
                                            <Select.Option value=''></Select.Option>
                                            <Select.Option value=''></Select.Option>
                                            <Select.Option value=''></Select.Option>
                                        </Select>
                                    )
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('institution', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('graduatetime', {
                                        rules: [{required: true}]
                                    })(<DatePicker.MonthPicker format='YYYY/MM' />)
                                }
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary"
                                        style={{margin: "50px 0 0 450px"}}
                                        onClick={()=>this.handleNext(0)}></Button>
                            </Form.Item>
                        </Form>
                    )
                },
                {
                    title: "",
                    content: (
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('workid', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('entrytime', {
                                        rules: [{required: true}]
                                    })(<DatePicker format='YYYY/MM/DD' />)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('department', {
                                        rules: [{required: true}]
                                    })(<Input  placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('duty', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('level', {
                                        rules: [{required: true}]
                                    })(
                                        <Radio.Group buttonStyle="solid">
                                            <Radio.Button value={0}> </Radio.Button>
                                            <Radio.Button value={1}> </Radio.Button>
                                            <Radio.Button value={2}> </Radio.Button>
                                        </Radio.Group>
                                    )
                                }
                            </Form.Item>
                            <Form.Item label="Tel">
                                {
                                    this.props.form.getFieldDecorator('tel', {
                                        rules: [{required: true}]
                                    })(<Input addonBefore="+86" placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('address', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('salary', {
                                        rules: [{required: true}]
                                    })(<Input addonAfter="" placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('profile', {
                                        rules: [{required: true}]
                                    })(<PicturesWall />)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('note', {
                                        initialValue: ""
                                    })(<Input.TextArea rows={4} placeholder="" /> )
                                }
                            </Form.Item>
                            <Form.Item>
                                <Input.Group style={{marginLeft: "410px"}}>
                                    <Button type="primary" style={{marginRight: "50px"}} onClick={()=>this.handlePrevious()}></Button>
                                    <Button type="primary" htmlType="submit"></Button>
                                </Input.Group>
                            </Form.Item>
                        </Form>
                    )
                },
                {
                    title: "",
                    content: (
                        <Card
                            style={{ width: "80%", margin: "20px auto"}}
                            tabList={[{key: 'tab1', tab: ''}, {key: 'tab2', tab: ''}]}
                            activeTabKey={this.state.key}
                            onTabChange={key => {
                                this.onTabChange(key);
                            }}
                        >
                            {contentList[this.state.key]}
                        </Card>
                    )
                }
            ];
            return (
                <div>
                    <TopNav nav={['', '', '']} />
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/employee')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Steps current={this.state.current} style={{margin: "40px auto", width: "550px"}}>
                            {
                                steps.map((item, index) =>
                                    <Steps.Step key={index} title={item.title}/>)
                            }
                        </Steps>
                        <div className="steps-content">{steps[this.state.current].content}</div>
                    </Card>
                </div>
            )
        }
    }
);

export default AddEmployee;