import React from 'react';
import {Table, Divider, Switch, message, Input, Button, Modal} from 'antd';
import Loading from '../../components/loading';
import TopNav from '../../components/top-nav'
import {reqCategoryList, updateCategoryList, reqCategorySearch, reqCategoryDelete} from '../../api';

class Category extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isLoading: true,
            data: [],
        }
    }
    UNSAFE_componentWillMount = async () => {
        const response=await reqCategoryList();
        this.refreshTable(response);
    };
    refreshTable  = (response) => {
        this.setState({isLoading: true});
        this.timerID = setTimeout(() => {
            if(response.status===0){
                let data = response.data.map((item, index) => {
                    item.index = index;
                    return item
                });
                this.setState({isLoading: false, data: data})
            }else if(response.status===1){
                message.error(response.msg);
                this.setState({isLoading: false, data: null})
            }
        }, 300)
    };
    handleIsnavChange = async (record) =>{
        const response = await updateCategoryList({id: record.id, isnav: 1-record.isnav});
        let data = this.state.data;
        if(response.status === 0){
            data.forEach(item => item.isnav = item.id===record.id ? 1-item.isnav : item.isnav);
            this.setState({
                data: data
            });
        }else if(response.status === 1){
            this.setState({
                data: data
            });
            message.error(response.msg)
        }
    };
    handleSearch = async (value) => {
        const response = value==="" ? await reqCategoryList() : await reqCategorySearch(value);
        this.refreshTable(response);
    };
    handleDelete = (record) => {
        Modal.confirm({
            title: '',
            content:  "\""+record.name+"\"",
            okText: '',
            onOk: async () => {
                const response = await reqCategoryDelete(record.id);
                if(response.status === 0){
                    let data=[];
                    this.state.data.forEach(item => {
                        item.id===record.id ? data.push() : data.push(item)
                    });
                    this.setState({
                        data: data
                    })
                }
            },
            cancelText: '',
            onCancel: () => {}
        });
    };
    handleAdd = () => {this.props.history.push('/category-add')};
    handleEdit = (record) => {
        this.props.history.push({
            pathname: '/category-edit',
            state: {data: record}
        })
    };
    handleView = (record) => {
        this.props.history.push({pathname: '/category-view', state: {data: record}})
    };
    render(){
        const columns = [
            {
                title: '',
                key: 'index',
                render: (text, record) => <span>{record.index+1}</span>
            },
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
                render: (text, record) => <Button type="link" onClick={()=>this.handleView(record)}>{text}</Button>
            },
            {
                title: '',
                dataIndex: 'level',
                key: 'level',
                render: text => {
                    let level="";
                    switch(text){
                        case 0 : level="";break;
                        case 1 : level="";break;
                        case 2 : level="";break;
                        case 3 : level="";break;
                        case 4 : level="";break;
                        case 5 : level="";break;
                        case 6 : level="";break;
                        case 7 : level="";break;
                        case 8 : level="";break;
                        default: level=""
                    }
                    // return <Button type="link" onClick={this.testfunc}>{level}</Button>
                    return <span>{level}</span>;
                },
                sorter: (a, b) => a.level-b.level
            },
            {
                title: '',
                dataIndex: 'son',
                key: 'son',
                sorter: (a, b) => a.son-b.son
            },
            {
                title: '(/)',
                dataIndex: 'amount',
                key: 'amount',
                sorter: (a, b) => a.amount-b.amount
            },
            {
                title: '',
                dataIndex: 'isnav',
                key: 'isnav',
                render: (text, record) => <Switch
                    size="small"
                    onClick={()=>this.handleIsnavChange(record)}
                    defaultChecked={text === 1}/>,
                filters: [{text: '', value: 1}, {text: '', value: 0}],
                onFilter: (value, record) => value===record.isnav
            },
            {
                title: '',
                key: 'action',
                render: (record) => (
                    <span>
                    <Button size="small"  onClick={()=>this.handleEdit(record)}></Button>
                    <Divider type="vertical" />
                    <Button size="small" type="danger" onClick={()=>this.handleDelete(record)}></Button>
                </span>
                )
            }
        ];
        return (
            <div className="category-container">
                <TopNav nav={['', '']}/>
                <div style={{margin: "20px 22px 0 20px"}}>
                    <Button type="primary" onClick={this.handleAdd}></Button>
                    <Input.Search
                        style={{width: "200px", float: "Right"}}
                        placeholder=""
                        onSearch={(value) => this.handleSearch(value)}
                        enterButton
                    />
                </div>
                {
                    this.state.isLoading ? <Loading /> :
                        <Table
                            style={{margin: "20px"}}
                            size="small"
                            columns={columns}
                            dataSource={this.state.data}
                            rowKey={record => record.id}
                            bordered
                        />
                }
            </div>
        )
    }
}

export default Category;




















