import React from 'react';
import {Form, Input, Card, Button, message, Select} from 'antd';
import TopNav from "../../components/top-nav";
import {reqRoleList, reqUserAdd} from "../../api";

const AddUser = Form.create({ name: 'user-add' })(
    class extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                options: []
            }
        }
        UNSAFE_componentWillMount = async () => {
            const response = await reqRoleList();
            if(response.status === 0){
                let options = [];
                response.data.forEach(item => {
                    options.push({key: item.id, value: item.name})
                });
                this.setState({options: options});
            }
        };
        
        validateName = (rule, value, callback) =>{
            if(!value){
                callback("")
            }else if(value.length>10){
                callback("10")
            }else{
                callback()
            }
        };
        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFields( async (err, values) => {
                if(!err) {
                    let data=values;
                    data.createtime = new Date().getTime();
                    console.log(values);
                    const response = await reqUserAdd(data);
                    if(response.status === 0){
                        message.success(",");
                        this.timerID = setTimeout(()=>{
                            this.props.history.push('/user')
                        }, 2000);
                    }
                }
            });
        };
        render() {
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            const form = this.props.form;
            return (
                <div>
                    <TopNav nav={['','','']}/>
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/user')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="">
                                {form.getFieldDecorator('username', {
                                    rules: [{required: true}],
                                })(<Input placeholder=""/>)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('password', {
                                    rules: [{required: true}],
                                })(<Input placeholder=""/>)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('role', {
                                    rules: [{required: true}]
                                })(
                                    <Select placeholder="">
                                        {
                                            this.state.options.map((item) => {
                                                return <Select.Option key={item.key}>{item.value}</Select.Option>
                                            })
                                        }
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item label="Tel">
                                {form.getFieldDecorator('tel', {
                                    rules: [{required: true}],
                                })(<Input placeholder="" addonBefore="+86" />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('email', {
                                    rules: [{required: true}],
                                })(<Input placeholder="" />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('note', {
                                    initialValue: ""
                                })(<Input.TextArea rows={4}/>)}
                            </Form.Item>
                            <Form.Item>
                                <Button style={{margin: "50px 0 0 460px"}} type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Card>
                </div>
            )
        }
    },
);

export default AddUser;








