import React from 'react';
import {Form, Card, Input, Button, Select, InputNumber, Modal, message} from 'antd';
import TopNav from '../../components/top-nav/index';
import PictureWall from "../../components/picture-wall/picture-wall";
import {reqStockCategory, addStocklogList, addStockList} from "../../api";
import memoryUtils from "../../util/memoryUtils";

const AddStock = Form.create({name: 'add-stock-form'})(
    class extends React.Component{
        constructor(props){
            super(props);
            this.state = {
                category: [],
                categoryAdd: "",
                visible: false
            }
        }
        UNSAFE_componentWillMount = async () => {
            const response = await reqStockCategory();
            if(response.status === 0){
                let category = [];
                response.data.forEach(item => {
                    category.push(item.category)
                });
                this.setState({category: category})
            }
        };
        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFields( async (err, values) => {
                if(!err){
                    let data = values;
                    let response1 = await addStockList(data);
                    if(response1.status===0){
                        let log = {...data};
                        log.stockname = log.name;
                        log.manager = memoryUtils.user.username;
                        log.operation = "";
                        log.time = new Date().getTime();
                        log.stockid = response1.data.insertId;
                        delete log.name;
                        let response2 = await addStocklogList(log);
                        if(response2.status === 0){
                            message.success(",", 2);
                            this.timerID = setTimeout(()=>{
                                this.props.history.push('/stock')
                            }, 2000);
                        }
                    }
                }
            })
        };
        handleAddClick = () => {
            this.setState({visible: true})
        };
        handleNameChange = (e) => {
            this.setState({categoryAdd: e.target.value})
        };
        onOk = () => {
            let category = this.state.category;
            category.push(this.state.categoryAdd);
            this.setState({
                category: category,
                visible: false
            })
        };
        onCancel = () => {
            this.setState({visible: false})
        };
        render(){
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            return (
                <div>
                    <TopNav nav={['', '', '']}/>
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/stock')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('name', {
                                        rules: [{required: true}]
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('category', {
                                        rules: [{required: true}]
                                    })(
                                        <Select style={{width: "75%"}} placeholder="">
                                            {this.state.category.map(item => (
                                                <Select.Option key={item}>{item}</Select.Option>
                                            ))}
                                        </Select>
                                    )
                                }
                                {
                                    <div style={{display: "inline-block", float: "right"}}>
                                        <Button type="primary"
                                                onClick={this.handleAddClick}></Button>
                                        <Modal
                                            title=""
                                            visible={this.state.visible}
                                            okText=""
                                            onOk={this.onOk}
                                            cancelText=""
                                            onCancel={this.onCancel}
                                        >
                                            <Input placeholder="" onChange={this.handleNameChange}/>
                                        </Modal>
                                    </div>
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('price', {
                                        rules: [{required: true}]
                                    })(<InputNumber />)
                                }
                                <span style={{marginLeft: "10px"}}></span>
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('amount', {
                                        rules: [{required: true}]
                                    })(<Input placeholder="" />)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('unit', {
                                        rules: [{required: true}]
                                    })(<Input placeholder="" />)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                <Input.Group compact>
                                    <Form.Item>
                                        {
                                            this.props.form.getFieldDecorator('excess', {
                                                rules: [{required: true}]
                                            })(<InputNumber placeholder=""/> )
                                        }
                                    </Form.Item>
                                    <span style={{margin: "8px 20px 0 20px"}}>~</span>
                                    <Form.Item>
                                        {
                                            this.props.form.getFieldDecorator('warning', {
                                                rules: [{required: true}]
                                            })(<InputNumber placeholder=""/> )
                                        }
                                    </Form.Item>
                                </Input.Group>
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('pictures', {
                                        rules: [{required: true, message: ""}]
                                    })(<PictureWall />)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('note', {
                                        initialValue: ""
                                    })(<Input.TextArea rows={4} placeholder="" /> )
                                }
                            </Form.Item>
                            <Form.Item>
                                <Button style={{margin: "50px 0 0 460px"}} type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Card>
                </div>
            )
        }
    }
);

export default AddStock;