import React from 'react';
import TopNav from "../../components/top-nav";
import {Button, Divider, Icon, Input, Table, Tag, Modal} from "antd";
import Loading from "../../components/loading";
import { reqEmployeeList, reqEmployeeDelete, reqEmployeeSearch } from '../../api/index';

class Employee extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isLoading: true,
            data: [],
            dutyFilter: [],
            departmentFilter: []
        }
    }
    UNSAFE_componentWillMount = async () => {
        const response = await reqEmployeeList();
        this.refreshTable(response)
    };
    componentWillUnmount = () => {
        clearTimeout(this.timerID)
    };
    refreshTable = (response) => {
        if(response.status === 0){
            let department = [], departmentFilter = [], duty = [], dutyFilter = [];
            response.data.forEach(item => {
                department.push(item.department);
                duty.push(item.duty)
            });
            new Set(department).forEach(item => {
                departmentFilter.push({text: item, value: item})
            });
            new Set(duty).forEach(item => {
                dutyFilter.push({text: item, value: item})
            });
            this.setState({isLoading: true});
            this.timerID = setTimeout(() => {
                this.setState({
                    isLoading: false,
                    data: response.data,
                    departmentFilter: departmentFilter,
                    dutyFilter: dutyFilter
                })
            })
        }
    };
    handleSearch = async (nameOrWorkid) => {
        const response = nameOrWorkid==="" ? await reqEmployeeList() : await reqEmployeeSearch(nameOrWorkid);
        this.refreshTable(response)
    };
    handleDelete = (record) => {
        Modal.confirm({
            title: '',
            content: "\""+record.name+"("+record.workid+")\"",
            okText: '',
            onOk: async () => {
                const response = await reqEmployeeDelete(record.workid);
                if(response.status === 0){
                    let data = [];
                    this.state.data.forEach(item => {
                        item.workid===record.workid ? data.push() : data.push(item)
                    });
                    this.setState({data})
                }
            },
            cancelText: '',
            onCancel: () => {}
        })
    };
    handleAdd = () => {
        this.props.history.push("/employee-add")
    };
    handleView = (record) => {
        this.props.history.push({pathname: '/employee-view', state: {data: record}})
    };
    handleEdit = (record) => {
        this.props.history.push({pathname: '/employee-edit', state: {data: record}})
    };
    render(){
        const columns = [
            {
                title: '',
                key: 'workid',
                dataIndex: 'workid'
            },
            {
                title: '/',
                key: 'name',
                dataIndex: 'name',
                render: (text, record) => (
                    <Button type="link" onClick={()=>this.handleView(record)}>
                        {text}
                        {record.gender===0 ? <Icon type="woman"/> : <Icon type="man"/>}
                    </Button>
                ),
                filters: [{text: '', value: 1},{text: '', value: 0}],
                onFilter: (value, record) => value===record.gender
            },
            {
                title: '',
                key: 'age',
                dataIndex: 'age',
                sorter: (a, b) => a.age-b.age
            },
            {
                title: '',
                key: 'tel',
                dataIndex: 'tel',
                render: (text)=><span><Icon type="phone" /> {text}</span>
            },
            {
                title: '',
                key: 'workage',
                dataIndex: 'entrytime',
                render: (text) => <span>{new Date().getFullYear()-new Date(text).getFullYear() || ""}</span>,
                sorter: (a, b) => a.entrytime-b.entrytime
            },
            {
                title: '',
                key: 'department',
                dataIndex: 'department',
                filters: this.state.departmentFilter,
                onFilter: (value, record) => value===record.department
            },
            {
                title: '',
                key: 'duty',
                dataIndex: 'duty',
                render: (text,record) => record.level===0 ? <span>{text} <Tag color="blue"></Tag></span> :
                    record.level===1 ? <span>{text} <Tag color="green"></Tag></span> :
                        <span>{text} <Tag color="red"></Tag></span>,
                filters: this.state.dutyFilter,
                onFilter: (value, record) => value===record.duty
            },
            {
                title: '',
                key: 'salary',
                dataIndex: 'salary',
                render: (text) => <span>{text}</span>,
                sorter: (a, b) => a.salary-b.salary
            },
            {
                title: '',
                key: 'operation',
                render: (record) => (
                    <div>
                        <Button size="small" type="primary" onClick={()=>this.handleEdit(record)}></Button>
                        <Divider type="vertical"/>
                        <Button size="small" type="danger" onClick={()=>this.handleDelete(record)}></Button>
                    </div>
                )
            }
        ];
        return (
            <div>
                <TopNav nav={['', '']} />
                <div style={{margin: "20px 22px 0 20px"}}>
                    <Button type="primary" onClick={this.handleAdd}></Button>
                    <Input.Search
                        style={{width: "200px", float: "Right"}}
                        placeholder=""
                        onSearch={(value) => this.handleSearch(value)}
                        enterButton
                    />
                </div>
                {
                    this.state.isLoading ? <Loading/> :
                        <Table size="small"
                               style={{margin: "20px"}}
                               columns={columns}
                               dataSource={this.state.data}
                               rowKey={record => record.workid}
                               bordered
                        />
                }
            </div>
        )
    }
}

export default Employee;