import React from 'react';
import { Form, Card, Button, Input, message, InputNumber, Radio} from 'antd';
import TopNav from "../../components/top-nav";
import { updateCustomerList } from '../../api/index'

const EditCustomer = Form.create({name: 'add-customer-form'})(
    class extends React.Component{
        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFields(async (err, values) => {
                if(!err){
                    let data = {};
                    data.id = this.props.location.state.data.id;
                    for(let key in values){
                        if(values.hasOwnProperty(key)){
                            data[key] = values[key];
                        }
                    }
                    const response = await updateCustomerList(data);
                    if(response.status === 0){
                        message.success("");
                        this.timerID = setTimeout(() => {
                            this.props.history.push('/customer')
                        }, 2000)
                    }
                }
            })
        };
        render(){
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            const data = this.props.location.state.data;
            return (
                <div>
                    <TopNav nav={['', '', '']} />
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/customer')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="" hasFeedback>
                                {
                                    this.props.form.getFieldDecorator('name', {
                                        rules: [{required: true}],
                                        initialValue: data.name
                                    })(<Input placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="Tel" hasFeedback>
                                {
                                    this.props.form.getFieldDecorator('tel', {
                                        rules: [{required: true}],
                                        initialValue: data.tel
                                    })(<Input addonBefore="+86" placeholder=""/>)
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('status', {
                                        initialValue: data.status,
                                        rules: [{required: true}]
                                    })(
                                        <Radio.Group buttonStyle="solid">
                                            <Radio.Button value={1}></Radio.Button>
                                            <Radio.Button value={2}></Radio.Button>
                                            <Radio.Button value={3}></Radio.Button>
                                        </Radio.Group>
                                    )
                                }
                            </Form.Item>
                            <Form.Item label="">
                                {
                                    this.props.form.getFieldDecorator('integral', {
                                        initialValue: data.integral
                                    })(<InputNumber style={{width: "100px"}} placeholder="" /> )
                                }
                            </Form.Item>
                            <Form.Item label="" hasFeedback>
                                {
                                    this.props.form.getFieldDecorator('note', {
                                        initialValue: data.note
                                    })(<Input.TextArea rows={4} placeholder="" /> )
                                }
                            </Form.Item>
                            <Form.Item>
                                <Button style={{margin: "50px 0 0 460px"}} type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Card>
                </div>
            )
        }
    }
);

export default EditCustomer;