import React from 'react';
import {Button, Input, Table, Divider, Icon, Modal, Tag, message, Form, Popconfirm} from 'antd';
import TopNav from '../../components/top-nav/index';
import Loading from '../../components/loading/index';
import { reqCustomerList, reqCustomerSearch, reqCustomerDelete, reqRuleList, reqRuleUpdate } from '../../api/index';
import {InputNumber} from "antd/es";

class Customer extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isLoading: true,
            data: [],
            visible: false,
            rule: {}
        }
    }
    UNSAFE_componentWillMount = async () => {
        let response = await reqCustomerList();
        this.refreshTable(response)
    };
    componentWillUnmount = () => {
        clearTimeout(this.timerID)
    };
    refreshTable = (response) => {
        if(response.status === 0){
            let data = response.data;
            data = data.map(item => {
                return item
            });
            this.setState({isLoading: true});
            this.timerID = setTimeout(() => {
                this.setState({
                    isLoading: false,
                    data: data,
                })
            }, 300)
        }
    };
    handleSearch = async (idOrName) => {
        const response = idOrName==="" ? await reqCustomerList() : await reqCustomerSearch(idOrName);
        this.refreshTable(response)
    };
    handleAdd = () => {
        this.props.history.push('/customer-add')
    };
    handleSet = async () => {
        const response = await reqRuleList();
        if(response.status === 0){
            this.setState({
                visible: true,
                rule: response.data[0]
            })
        }
    };
    handleDelete = (record) => {
        Modal.confirm({
            title: '',
            content: "\""+record.name+"(ID:"+record.id+")\"?",
            okText: '',
            onOk: async ()=>{
                const response1 = await reqCustomerDelete(record.id);
                if(response1.status === 0){
                    let data = [];
                    this.state.data.forEach(item => {
                        item.id===record.id ? data.push() : data.push(item)
                    });
                    this.setState({data: data})
                }
            },
            onCancel: ()=>{}
        })
    };
    handleDetail = (record) => {
        this.props.history.push({pathname: '/customer-view', state: {data: record}})
    };
    handleEdit = (record) => {
        this.props.history.push({pathname: '/customer-edit', state: { data: record}})
    };
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields(async (err, values) => {
            if(!err){
                const response = await reqRuleUpdate(values);
                if(response.status === 0){
                    this.setState({visible: false});
                    this.timerID = setTimeout(()=>{
                        message.success("");
                    }, 500)
                }
            }
        })
    };
    render(){
        const columns = [
            {
                title: 'ID',
                key: 'id',
                dataIndex: 'id'
            },
            {
                title: '/',
                key: 'name',
                dataIndex: 'name',
                render: (text, record) => (
                    <Button type="link" onClick={()=>this.handleDetail(record)}>
                        {
                            record.status===1 ? <span>{text} <Tag color="blue"></Tag></span> :
                                record.status===2 ? <span>{text} <Tag color="green"></Tag></span> :
                                    <span>{text} <Tag color="red"></Tag></span>
                        }
                    </Button>
                ),
                filters: [{text: '', value: 1}, {text: '', value: 2}, {text: '', value: 3}],
                onFilter: (value, record) => value===record.status
            },
            {
                title: '',
                key: 'createtime',
                dataIndex: 'createtime',
                render: (text) => {
                    let time = new Date(text);
                    let month = time.getMonth()+1;
                    return <span><Icon type="clock-circle" /> {time.getFullYear()+'-'+month+'-'+time.getDate()}</span>;
                },
                sorter: (a, b) => a.createtime-b.createtime,
            },
            {
                title: '',
                key: 'tel',
                dataIndex: 'tel',
                render: (text)=><span><Icon type="phone" /> {text}</span>
            },
            {
                title: '',
                key: 'integral',
                dataIndex: 'integral',
                sorter: (a, b) => a.integral-b.integral
            },
            {
                title: '',
                key: 'orderSum',
                render: (text, record) => <span></span>,
                filters: [
                    {
                        text: '',
                        value: '0',
                    },
                    {
                        text: '',
                        value: '1',
                    },
                    {
                        text: '',
                        value: '2'
                    }
                ]
            },
            {
                title: '',
                key: 'orderAmount',
                render: (text, record) => <span></span>,
                filters: [
                    {
                        text: '',
                        value: '0',
                    },
                    {
                        text: '',
                        value: '1',
                    },
                    {
                        text: '',
                        value: '2'
                    }
                ]
            },
            {
                title: '',
                key: 'operation',
                render: (record) => (
                    <div>
                        <Button size="small" type="primary" onClick={()=>this.handleEdit(record)}></Button>
                        <Divider type="vertical"/>
                        <Button size="small" type="danger" onClick={()=>this.handleDelete(record)}></Button>
                    </div>
                )
            }
        ];
        return (
            <div className="customer-contianer">
                <TopNav nav={["",""]}/>
                <div style={{margin: "20px 22px 0 20px"}}>
                    <Button type="primary" onClick={this.handleAdd}></Button>
                    <Button type="primary" style={{marginLeft: "40px"}} onClick={this.handleSet}></Button>
                    <Input.Search
                        style={{width: "200px", float: "Right"}}
                        placeholder=""
                        onSearch={(value) => this.handleSearch(value)}
                        enterButton
                    />
                </div>
                {
                    this.state.isLoading ? <Loading /> :
                        <Table size="small"
                               style={{margin: "20px"}}
                               columns={columns}
                               dataSource={this.state.data}
                               rowKey={record => record.id}
                               bordered
                        />
                }
                <Modal
                    visible={this.state.visible}
                    title=""
                    onCancel={()=>{this.setState({visible: false})}}
                    footer={null}
                >
                    <Form style={{overflow: "hidden"}} labelCol={{ span: 8 }} wrapperCol={{ span: 16 }} onSubmit={this.handleSubmit}>
                        <Form.Item label="ID" style={{display: "none"}}>
                            {
                                this.props.form.getFieldDecorator('id', {
                                    initialValue: 1000
                                })(<InputNumber/>)
                            }
                        </Form.Item>
                        <Form.Item label="">
                            {
                                this.props.form.getFieldDecorator('gold', {
                                    initialValue: this.state.rule.gold || 0
                                })(<InputNumber style={{width: "150px"}}/>)
                            }
                            <span style={{marginLeft: "10px"}}></span>
                        </Form.Item>
                        <Form.Item label="">
                            {
                                this.props.form.getFieldDecorator('platinum', {
                                    initialValue: this.state.rule.platinum || 0
                                })(<InputNumber style={{width: "150px"}}/>)
                            }
                            <span style={{marginLeft: "10px"}}></span>
                        </Form.Item>
                        <Form.Item label="">
                            {
                                this.props.form.getFieldDecorator('diamond', {
                                    initialValue: this.state.rule.diamond || 0
                                })(<InputNumber style={{width: "150px"}}/>)
                            }
                            <span style={{marginLeft: "10px"}}></span>
                        </Form.Item>
                        {/*<Button style={{float: "right", margin: "20px 160px 0 30px"}} type="primary" htmlType="submit"></Button>*/}
                        <Popconfirm
                            placement="topRight"
                            title=""
                            okType="danger"
                            onConfirm={this.handleSubmit}
                            okText=""
                            cancelText=""
                        >
                            <Button style={{float: "right", margin: "20px 160px 0 30px"}} type="primary"></Button>
                        </Popconfirm>
                        <Button
                            style={{float: "right", marginTop: "20px"}}
                            type="primary"
                            onClick={()=>{this.setState({visible: false})}}
                        ></Button>
                    </Form>
                </Modal>
            </div>
        )
    }
}

export default Form.create()(Customer);





















