import React from 'react';
import TopNav from "../../components/top-nav";
import {Button, Card, Form} from "antd";

class ViewCategory extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            data: {}
        }
    }
    UNSAFE_componentWillMount = async () => {
        let data = this.props.location.state.data;
        this.setState({
            data: data
        })
    };
    render() {
        const formItemLayout = {
            labelCol: {span: 8},
            wrapperCol: {span: 10}
        };
        return (
            <div>
                <TopNav nav={['', '', '']} />
                <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                      extra={<Button type="primary" onClick={()=>{this.props.history.push('/category')}}></Button>}
                      style={{width: "100%", border: "none"}}
                >
                    <Form {...formItemLayout} style={{marginTop: "40px", textIndent: "20px"}} onSubmit={this.handleSubmit}>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.name}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.level}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.isnav}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.son}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.amount}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.note}</span>
                        </Form.Item>
                    </Form>
                </Card>
            </div>
        )
    }
}

export default ViewCategory;


