import React from 'react';
import './product.less';
import {Form, Button, Card, Icon} from 'antd';
import TopNav from "../../components/top-nav";

class ViewProduct extends React.Component{
    constructor(props){
        super(props);
        this.state=({
            data: {}
        })
    }
    UNSAFE_componentWillMount = async () => {
        let data = this.props.location.state.data;
        this.setState({
            data: data
        })
    };
    render(){
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 10 },
        };
        let stateYes = (string) => (<span><Icon type="check" style={{color: "#1DA57A"}}/>{string}</span>);
        let stateNo = (string) => (<span><Icon type="close" style={{color: "#1DA57A"}}/>{string}</span>);
        return (
            <div className="product-view-container">
                <TopNav nav={['', '', '']}/>
                <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                      extra={<Button type="primary" onClick={()=>this.props.history.push('/product')}></Button>}
                      style={{ border: "none", width: "100%"}}
                >
                    <Form {...formItemLayout} style={{marginTop: "40px"}}>
                        <Form.Item label="" hasFeedback>
                            <span className="ant-form-text">{this.state.data.number}</span>
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            <span className="ant-form-text">{this.state.data.name}</span>
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            <span className="ant-form-text">{this.state.data.category_name}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.price}</span>
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            <span className="ant-form-text">{this.state.data.unit}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text" style={{marginLeft: "-20px"}}>
                                {this.state.data.onsale ? stateYes("") : stateNo("")}
                                {this.state.data.onsale ? stateYes("") : stateNo("")}
                                {this.state.data.onsale ? stateYes("") : stateNo("")}
                            </span>
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            <span className="ant-form-text">{this.state.data.integral}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.introduce}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.description}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">
                                {this.state.data.discount_gold===10 ? "" : this.state.data.discount_gold+"" }
                            </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">
                                {this.state.data.discount_platinum===10 ? "" : this.state.data.discount_platinum+"" }
                            </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">
                                {this.state.data.discount_diamond===10 ? "" : this.state.data.discount_diamond+"" }
                            </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-pictures">
                                {
                                    this.state.data.pictures==="" ? <span></span> :
                                        this.state.data.pictures.split(",").map((item, index)=> {
                                            return (<img key={index} src={item} alt=""/>)
                                        })
                                }
                            </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.state.data.note}</span>
                        </Form.Item>
                    </Form>
                </Card>
            </div>
        )
    }
}

export default Form.create({name:'product-add-form'})(ViewProduct);
