import React from 'react';
import {Form,Input,Icon,Button,message} from 'antd';
import {Redirect} from 'react-router-dom';
import './login.less';
import logo from '../../assets/images/logo.png';
import {reqLogin, reqRoleList} from '../../api';
import memoryUtils from '../../util/memoryUtils';
import storageUtils from '../../util/storageUtils';
//
class Login extends React.Component{
    handleSubmit=(e)=>{
        e.preventDefault();
        this.props.form.validateFields(async (err,values)=>{
            if(!err){
                const {username,password}=values;
                const response1 = await reqLogin(username,password);
                const response2 = await reqRoleList();
                if(response1.status===0 && response2.status===0){
                    message.success('');
                    let data = response1.data;
                    let permission = [];
                    response2.data.forEach(item => {
                        if(response1.data.role===item.id) permission = item.permission.split("-")
                    });
                    data.permission = permission;
                    memoryUtils.user = response1.data;
                    storageUtils.saveUser(response1.data);
                    this.props.history.replace('/')
                }
            }else{
                console.log("")
            }
        })
    };
    validatePwd=(rule,value,callback)=>{
        if(!value){
            callback("")
        }else if(value.length<4){
            callback("4")
        }else if(!/^[a-zA-Z0-9_]+$/.test(value)){
            callback("")
        }else if(value.length>12){
            callback("12")
        }else{
            callback()
        }
    };
    validateUser=(rule,value,callback)=>{
        if(!value){
            callback("")
        }else if(value.length<4){
            callback("4")
        }else if(!/^[a-zA-Z0-9_]+$/.test(value)){
            callback("")
        }else if(value.length>12){
            callback("12")
        }else{
            callback()
        }
    };
    render(){
        const user=memoryUtils.user;
        if(JSON.stringify(user)!=='{}'){
            return <Redirect to="/" />
        }
        const form=this.props.form;
        return (
            <div className="login">
                <header className="login-header">
                    <img src={logo} alt="logo"/>
                    <h3></h3>
                </header>
                <section className="login-content">
                    <h2></h2>
                    <Form onSubmit={this.handleSubmit} className="login-form">
                        <Form.Item>
                            {
                                form.getFieldDecorator('username',{
                                    rules:[
                                        {validator:this.validateUser}
                                    ]
                                })(
                                    <Input
                                        prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                        placeholder=""
                                    />
                                )
                            }
                        </Form.Item>
                        
                        <Form.Item>
                            {
                                form.getFieldDecorator('password',{
                                    rules:[
                                        {validator:this.validatePwd}
                                    ]
                                })(
                                    <Input
                                        prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                        type="password"
                                        placeholder=""
                                    />
                                )
                            }
                        </Form.Item>
                        <Form.Item>
                            <Button type="primary" htmlType="submit" className="login-form-button">
                                
                            </Button>
                        </Form.Item>
                    </Form>
                </section>
            </div>
        )
    }
   
}

export default Form.create()(Login);

