import React from 'react';
import {Button, Select, Form, Input} from 'antd'

const ProductSearch = Form.create({name: 'product-operate-form'})(
    class extends React.Component{
        render(){
            return (
                <Form layout="inline" onSubmit={this.props.onSubmit}>
                    <Form.Item>
                        { this.props.form.getFieldDecorator('status', {
                            initialValue: []
                        })(<Select style={{width: "110px", textAlign: "left"}} placeholder="">
                            <Select.Option value=""></Select.Option>
                            <Select.Option value="onsale-1"></Select.Option>
                            <Select.Option value="onsale-0"></Select.Option>
                            <Select.Option value="isnew-1"></Select.Option>
                            <Select.Option value="isnew-0"></Select.Option>
                            <Select.Option value="recommend-1"></Select.Option>
                            <Select.Option value="recommend-0"></Select.Option>
                        </Select>)}
                    </Form.Item>
                    <Form.Item>
                        { this.props.form.getFieldDecorator('nameOrNumber', {
                            initialValue: ""
                        })(<Input placeholder="/"
                                  style={{width: "120px", textAlign: "left"}} />)}
                    </Form.Item>
                    <Form.Item>
                        <Button.Group>
                            <Button type="primary" htmlType="submit"></Button>
                        </Button.Group>
                    </Form.Item>
                </Form>
            )
        }
    }
);

export default ProductSearch