import React from 'react';
import { Form, Select, InputNumber, Button, Checkbox, Row, Col, Card, Input, message } from 'antd';
import TopNav from '../../components/top-nav';
import PictureWall from '../../components/picture-wall/picture-wall';
import { addProductList, reqCategoryList } from '../../api';


class AddProduct extends React.Component {
    constructor(props){
        super(props);
        this.state=({
            category_options: [],
        })
    }
    UNSAFE_componentWillMount = async () => {
        const response = await reqCategoryList();
        if(response.status === 0){
            let options = [];
            response.data.forEach((item) => {
                options.push({key: item.name, value: item.id})
            });
            this.setState({category_options: options})
        }
    };
    componentWillUnmount = () => {
        clearTimeout(this.timerID)
    };
    validateNumber = (rule, value, callback) => {
        if(!value){
            callback("")
        }else if(value.length<5 || value.length>10){
            callback("510")
        }else if((/^[0-9a-zA-Z]*$/g).test(value)){
            callback("")
        }else{
            callback()
        }
    };
    validateName = (rule, value, callback) => {
        if(!value){
            callback("")
        }else if(value.length<2 || value.length>10){
            callback("210")
        }else{
            callback()
        }
    };
    validatePrice = (rule, value, callback) => {
        if(!value){
            callback("")
        }else if(value<0){
            callback("")
        }else{
            callback()
        }
    };
    validateUnit = (rule, value, callback) => {
        if(!value){
            callback("")
        }else{
            callback()
        }
    };
    validateNoteProduct = (rule, value, callback) => {
        if(!value){
            callback("")
        }else{
            callback()
        }
    };
    validateIntegral = (rule, value, callback) => {
        if(!value){
            callback("")
        }else{
            callback()
        }
    };
    validateIntroduceTitle = (rule, value, callback) => {
        if(!value){
            callback("")
        }else{
            callback()
        }
    };
    validateIntroduceContent =(rule, value, callback) => {
        if(!value){
            callback("")
        }else{
            callback()
        }
    };
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields(async (err, values) => {
            if (!err) {
                values.category = Number(values.category);
                values.price = Number(values.price);
                values.integral = Number(values.integral);
                values.onsale = values.state.indexOf('onsale')===-1 ? 0 : 1;
                values.isnew = values.state.indexOf('isnew')===-1 ? 0 : 1;
                values.recommend = values.state.indexOf('recommend')===-1 ? 0 : 1;
                delete values.state;
                const response = await addProductList(values);
                if(response.status === 0){
                    message.success(response.msg+",", 2);
                    this.timerID = setTimeout(()=>{
                        this.props.history.replace('/product')
                    }, 2000);
                }
            }else{
                console.log(err)
            }
        });
    };
    render() {
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 10 },
        };
        const {form} = this.props;
        return (
            <div>
                <TopNav nav={['', '']}/>
                <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                      extra={<Button type="primary" onClick={()=>{this.props.history.push('/product')}}></Button>}
                      style={{ border: "none", width: "100%"}}
                >
                    <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                        <Form.Item label="" hasFeedback>
                            { form.getFieldDecorator('number', {
                                rules: [{required: true, message: "", validate: this.validateNumber}]
                            })(<Input placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            { form.getFieldDecorator('name', {
                                rules: [{required: true, message: "", validate: this.validateName}]
                            })(<Input placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            { form.getFieldDecorator('category', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Select onChange={this.handleChange} placeholder="">
                                    { this.state.category_options.map((item) => {
                                        return <Select.Option key={item.value}>{item.key}</Select.Option>
                                    })}
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            { form.getFieldDecorator('price', {
                                rules: [{required: true, message: "",validate: this.validatePrice}]
                            })(<Input addonAfter=" &nbsp;&nbsp;&nbsp;" placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            { form.getFieldDecorator('unit', {
                                rules: [{required: true, message: "", validate: this.validateUnit}]
                            })(<Input placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="">
                            {form.getFieldDecorator('state', {
                                initialValue: ['isNew'],
                            })(
                                <Checkbox.Group style={{ width: '100%', margin: "10px 0 0 20px"}}>
                                    <Row>
                                        <Col span={8}>
                                            <Checkbox value="onsale"></Checkbox>
                                        </Col>
                                        <Col span={8}>
                                            <Checkbox value="isnew"></Checkbox>
                                        </Col>
                                        <Col span={8}>
                                            <Checkbox value="recommend"></Checkbox>
                                        </Col>
                                    </Row>
                                </Checkbox.Group>,
                            )}
                        </Form.Item>
                        <Form.Item label="" hasFeedback>
                            { form.getFieldDecorator('integral', {
                                rules: [{required: true, message: "", validate: this.validateIntegral}]
                            })(<Input placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="">
                            { form.getFieldDecorator('introduce', {
                                rule: [{validate: this.validateIntroduceTitle}]
                            })(<Input.TextArea rows={2} placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="">
                            { form.getFieldDecorator('description', {
                                rule: [{validate: this.validateIntroduceContent}]
                            })(<Input.TextArea rows={4} placeholder=""/>)}
                        </Form.Item>
                        <Form.Item label="">
                            {form.getFieldDecorator('discount_gold', { initialValue: 10 })(
                                <InputNumber min={1} max={10} step={0.5} />)}
                            <span className="ant-form-text"></span>
                        </Form.Item>
                        <Form.Item label="">
                            {form.getFieldDecorator('discount_platinum', { initialValue: 10 })(
                                <InputNumber min={1} max={10} step={0.5} />)}
                            <span className="ant-form-text"></span>
                        </Form.Item>
                        <Form.Item label="">
                            {form.getFieldDecorator('discount_diamond', { initialValue: 10 })(
                                <InputNumber min={1} max={10} step={0.5} />)}
                            <span className="ant-form-text"></span>
                        </Form.Item>
                        <Form.Item label="">
                            {form.getFieldDecorator('pictures', {
                                rules: [{required: true, message: ""}]
                            })(<PictureWall/>)}
                        </Form.Item>
                        <Form.Item label="">
                            { form.getFieldDecorator('note')(
                                <Input.TextArea rows={4} />
                            )}
                        </Form.Item>
                        <Form.Item>
                            <Button style={{margin: "50px 0 0 450px"}} type="primary" htmlType="submit"></Button>
                        </Form.Item>
                    </Form>
                </Card>
            </div>
    
            
            
        );
    }
}

export default Form.create({ name: 'add-product-form' })(AddProduct);

















