import React from 'react';
import {Form, Input, Card, Button, message, Switch, Select} from 'antd';
import TopNav from "../../components/top-nav";
import { addCategoryList } from "../../api";

const AddCategory = Form.create({ name: 'category-add' })(
    class extends React.Component {
        validateName = (rule, value, callback) =>{
            if(!value){
                callback("")
            }else if(value.length>10){
                callback("10")
            }else{
                callback()
            }
        };
        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFields( async (err, values) => {
                if(!err) {
                    let data=values;
                    data.level = Number(data.level);
                    data.isnav = data.isnav ? 1 : 0;
                    const response = await addCategoryList(data);
                    if(response.status === 0){
                        message.success(",", 2);
                        this.timerID = setTimeout(()=>{
                            this.props.history.push('/category')
                        }, 2000);
                    }
                }
            });
        };
        render() {
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            const form = this.props.form;
            return (
                <div>
                    <TopNav nav={['', '', '']}/>
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/category')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="">
                                {form.getFieldDecorator('name', {
                                    rules: [{required: true, validator: this.validateName }],
                                })(<Input />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('level', {
                                    rules: [{required: true}]
                                })(
                                    <Select>
                                        <Select.Option key={0}></Select.Option>
                                        <Select.Option key={1}></Select.Option>
                                        <Select.Option key={2}></Select.Option>
                                        <Select.Option key={3}></Select.Option>
                                        <Select.Option key={4}></Select.Option>
                                        <Select.Option key={5}></Select.Option>
                                        <Select.Option key={6}></Select.Option>
                                        <Select.Option key={7}></Select.Option>
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('isnav', {
                                    rules: [{required: true}],
                                    valuePropName: "checked",
                                    initialValue: false
                                })(<Switch />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('note', {
                                    initialValue: ""
                                })(<Input.TextArea rows={4}/>)}
                            </Form.Item>
                            <Form.Item>
                                <Button style={{margin: "50px 0 0 460px"}} type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Card>
                </div>
            )
        }
    },
);

export default AddCategory;








