import React from 'react';
import './customer.less';
import {Button, Card, Form, Tag} from 'antd';
import TopNav from "../../components/top-nav";

class ViewCustomer extends React.Component{
    getTime = (time) => {
        time = new Date(time);
        let month = time.getMonth()+1;
        return <span>{time.getFullYear()+'-'+month+'-'+time.getDate()}&nbsp;&nbsp;{time.getHours()+':'+time.getMinutes()}</span>;
    };
    render(){
        const formItemLayout = {
            labelCol: {span: 8},
            wrapperCol: {span: 10}
        };
        let data = this.props.location.state.data;
        return (
            <div className="customer-view-container">
                <TopNav nav={['', '', '']} />
                <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                      extra={<Button onClick={()=>{this.props.history.push('/customer')}}></Button>}
                      style={{width: "100%", border: "none"}}
                >
                    <Form {...formItemLayout} style={{marginTop: "40px"}}>
                        <Form.Item label="ID">
                            <span className="ant-form-text">{data.id}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{data.name}</span>
                        </Form.Item>
                        <Form.Item label="Tel">
                            <span className="ant-form-text">{data.tel}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{data.integral}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span style={{marginLeft: "20px"}}>
                                {
                                    data.status===1 ? <Tag color="blue"></Tag> :
                                        data.status===2 ? <Tag color="green"></Tag> : <Tag color="red"></Tag>
                                }
                            </span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{this.getTime(data.createtime)}</span>
                        </Form.Item>
                        <Form.Item label="">
                            <span className="ant-form-text">{data.note}</span>
                        </Form.Item>
                    </Form>
                </Card>
            </div>
        )
    }
}

export default ViewCustomer;